.userdetails {
  display: flex;
  align-items: center;
  gap: 10px;
}

.userdetails img {
  border-radius: 50%;
  height: 70px;
  width: 70px;
}
.details {
  display: flex;
  gap: 55px;
}
.details div {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.user {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.activebutton {
  padding-left: 3px;
  padding-right: 3px;
  height: 24px !important;
  background: #3c79e6 !important;
  border-radius: 4px !important;
  color: white !important;
  font-family: DM Sans !important;
  font-weight: bold !important;
  font-size: 12px !important;
  line-height: 16px !important;
  letter-spacing: 0.01em !important;
  text-transform: initial !important;
}

.expiredbtn {
  padding-left: 3px;
  padding-right: 3px;
  height: 24px !important;
  background: #dd0000 !important;
  border-radius: 4px !important;
  color: white !important;
  font-family: DM Sans !important;
  font-weight: bold !important;
  font-size: 12px !important;
  line-height: 16px !important;
  letter-spacing: 0.01em !important;
  text-transform: initial !important;
}

.inactivebutton {
  height: 24px !important;
  background: grey !important;
  padding-left: 3px;
  padding-right: 3px;
  border-radius: 4px !important;
  color: white !important;
  font-family: DM Sans !important;
  font-weight: bold !important;
  font-size: 12px !important;
  line-height: 16px !important;
  letter-spacing: 0.01em !important;
  text-transform: initial !important;
}

.vehicledetails {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.vehicledetails span {
  display: flex;
  align-items: center;
  gap: 10px;
}
