.heading {
  font-family: Poppins;
  font-style: normal;
  font-weight: 500;
  font-size: 22px;
  line-height: 33px;
  color: #b76c28;
}
.secondheading {
  font-family: Poppins;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #b76c28;
}
.subheading {
  display: flex;
  align-items: center;
  gap: 20px;
}
.subheading span {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #b76c28;
  color: white;
  display: grid;
  place-items: center;
}
.textfield {
  width: 255px !important;
  height: 38px !important;
}
.textfield input {
  padding: 10px;
}
.paginationdiv {
  display: flex;
  justify-content: space-between;
}
.pagination ul li button {
  color: #b76c28;
  border-radius: 0px;
  margin: 0 0;
  background-color: white;
}
.actionicons {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.searchfield {
  display: flex;
  justify-content: space-between;
}

.searchfield div {
  display: flex;
  gap: 16px;
  align-items: center;
}
.button1 {
  width: 70px !important;
  height: 32px !important;
  background: rgba(196, 196, 196, 0.3) !important;
  font-family: DM Sans !important;
  font-size: 12px !important;
  line-height: 16px !important;
}
.button2 {
  background: #b76c28 !important;
  border-radius: 5px !important;
  height: 30px !important;
  width: 200px !important;
  color: white !important ;
  font-family: DM Sans !important;
  font-weight: 500 !important;
  font-size: 16px !important;
  line-height: 21px !important;
  text-transform: initial !important;
}
.tabletitle {
  display: flex;
  align-items: center;
  gap: 11px;
}
