.heading {
    font-family: Poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 22px;
    line-height: 33px;
    color: #000000;
}
.dashboardItem {
    height: 100px;
    border: 1px solid rgba(0, 0, 0, 0.05);
    box-sizing: border-box;
    box-shadow: 4px 4px 5px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    display: flex;
    gap: 26px;
}
.dashboardItems {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(275px, 1fr));
    grid-gap: 13px;
}
.item {
    height: 100px;
    width: 100px;
    background: #e5e5e5;
    border-bottom-left-radius: 10px;
    border-top-left-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.itemdetails {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.title {
    font-family: DM Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 21px;
    color: #000000;
}
.span {
    font-family: DM Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 21px;
    color: #000000;
}
